import React, { Component } from "react";
import {
   Card,
   CardBody,
} from "reactstrap";
import { SetModal } from "../../components/common/SetModal";
import SetsList from "../../components/SetsList";
import { connect } from "react-redux";
import { Helmet } from "react-helmet";
import { AppRoutes } from "../../Config/AppRoutes";
import {
   getAllSetRequest,
   deleteSetReq,
   updateSetReq,
   updateSetStatusReq,
   modelOpenRequest,
   redirectTo
} from "../../actions";
import * as qs from "query-string";
import { isEqual } from "../../Helpers/Object";
class Sets extends Component {
   constructor(props) {
      super(props);
      this.state = {
         openCreate: false,
         openEdit: false,
         setData: {},
         setId: ""
      };
   }
   componentDidMount() {
      const query = qs.parse(this.props.location.search);
      this.props.getAllSetRequest({ ...query, page: query.page || 1 });
   }
   onPageChange = page => {
      const { location } = this.props;
      const { search } = location;
      const query = qs.parse(search);
      this.props.onGoPage(`${AppRoutes.SETS.url}?${qs.stringify({ page: page, search: query.search, sort: query.sort, status: query.status })}`);
   };
   componentDidUpdate({ setsReducer, location }) {
      const prevQuery = qs.parse(location.search);
      const currQuery = qs.parse(this.props.location.search);
      if (!isEqual(prevQuery, currQuery)) {
         this.props.getAllSetRequest({ ...currQuery, page: currQuery.page || 1 });
      }
   }
   toggleUpdateSet = dataValue => {
      this.setState({
         setData: dataValue.set,
      });
      const { modelDetails } = this.props.modelInfoReducer;
      let data = {
         editSetModal: !modelDetails.editSetModal,
      };
      this.props.modelOperate(data);
   };
   toggleEditSet = dataValue => {
      this.setState({
         setData: {}
      });
      const { modelDetails } = this.props.modelInfoReducer;
      let data = {
         editSetModal: !modelDetails.editSetModal,
      };
      this.props.modelOperate(data);
   };
   onSearch = data => {
      this.props.onGoPage(`${AppRoutes.SETS.url}?${qs.stringify({ page: data.page, search: data.search, sort: data.sort, status: data.status })}`);
   };
   deleteSet = setId => {

      const { location } = this.props;
      const { search } = location;
      const query = qs.parse(search);
      this.props.deleteSet({ ...query, setId });
   };
   onStatusUpdate = data => {
      const { location } = this.props;
      const { search } = location;
      const query = qs.parse(search);
      this.props.onStatusUpdate({ ...query, ...data });
   };
   updateSet = (id, data) => {
      const { location } = this.props;
      const { search } = location;
      const query = qs.parse(search);
      this.props.updateSet({ ...query, setId: id, ...data });

   }
   render() {
      const { setsReducer, modelInfoReducer, } = this.props;
      const { modelDetails } = modelInfoReducer;
      const { editSetModal } = modelDetails;
      return (
         <>
            <Helmet>
               <title>Superadmin/Folder</title>
            </Helmet>
            <Card className={"white-card"}>
               <CardBody className={"custom-card-body position-relative"}>
                  <SetsList
                     setData={setsReducer}
                     onPageChange={this.onPageChange}
                     onSearch={this.onSearch}
                     onDelete={this.deleteSet}
                     onUpdate={this.props.toggleUpdateSet}
                     openEdit={editSetModal}
                     onStatusUpdate={this.onStatusUpdate}
                     modelOperate={this.toggleUpdateSet}
                  />
               </CardBody>
            </Card>
            <SetModal
               setModalOpen={modelDetails.editSetModal}
               handleSetModal={this.toggleEditSet}
               updateSet={this.updateSet}
               setData={this.state.setData}
            />
         </>
      );
   }
}
const mapStateToProps = state => ({
   setsReducer: state.setsReducer,
   modelInfoReducer: state.modelInfoReducer
});

const mapDispatchToProps = dispatch => ({
   modelOperate: data => {
      dispatch(modelOpenRequest({ modelDetails: data }));
   },
   getAllSetRequest: data => {
      dispatch(getAllSetRequest(data));
   },
   updateSet: (data) => {
      dispatch(updateSetReq(data));
   },
   deleteSet: data => {
      dispatch(deleteSetReq(data));
   },
   onStatusUpdate: data => {
      dispatch(updateSetStatusReq(data));
   },
   onGoPage: data => {
      dispatch(redirectTo({ path: data }));
   }
});

export default connect(
   mapStateToProps,
   mapDispatchToProps
)(Sets);
