import React, { Component } from "react";
import {
   Button,
   Modal,
   ModalBody,
   ModalFooter,
   ModalHeader,
   Row,
   Col,
   FormGroup,
   Label,
   Input,
   Form,
   FormFeedback,
   InputGroup
} from "reactstrap";
import { logger } from "../../Helpers/Logger";
import Validator from "js-object-validation";
import {
   EditSetValidations,
   EditSetValidationsMessaages
} from "../../validations";
import LastUpdated from "../common/LastUpdated";

export class SetModal extends Component {
   constructor(props) {
      super(props);
      this.state = {
         title: "",
         description: "",
         isEditMode: false,
         errors: {},
      };
   }
   componentDidMount() {
      this.setState({ errors: {} })
   }

   componentDidUpdate({ setData }) {
      if (
         this.props.setData &&
         this.props.setData._id &&
         (setData._id !== this.props.setData._id) && this.props.setModalOpen) {
         const {
            title,
            description,
         } = this.props.setData;
         this.setState({
            isEditMode: true,
            title: title || "",
            description: description || "",
            errors: {}
         });
      }
   }
   handleInputChange = e => {
      const { target } = e;
      const { name, value } = target;
      this.setState({
         [name]: value,
         errors: {
            ...this.state.errors,
            [name]: null
         }
      });
   };
   editSet = e => {
      e.preventDefault();
      try {
         const {
            title,
            description,
         } = this.state;
         const payload = {
            title: title.trim(),
            description: description.trim(),
         };
         let { isValid, errors } = Validator(
            payload,
            EditSetValidations,
            EditSetValidationsMessaages
         );
         if (!isValid) {
            this.setState({
               errors
            });
            return;
         } else {
            this.props.updateSet(this.props.setData._id, payload);
         }
      } catch (error) {
         logger(error);
      }
   };
   render() {
      const { setModalOpen, handleSetModal, setData } = this.props;
      const {
         title,
         description,
         isEditMode,
         errors
      } = this.state;
      return (
         <>
            <Form onSubmit={this.editSet}>
               <Modal
                  isOpen={setModalOpen}
                  toggle={handleSetModal}
                  backdrop={"static"}
                  className="customer-modal custom-form-modal custom-modal-lg"
               >
                  <ModalHeader toggle={handleSetModal}>
                     {"Update set details"}
                     {isEditMode && setData && setData.updatedAt ? <LastUpdated updatedAt={setData.updatedAt} /> : null}
                  </ModalHeader>
                  <ModalBody>
                     <Row className="justify-content-center">
                        <Col md="6">
                           <FormGroup>
                              <InputGroup>
                                 <Label
                                    htmlFor="name"
                                    className="customer-modal-text-style"
                                 >
                                    Title <span className={"asteric"}>*</span>
                                 </Label>
                                 <div className={"input-block"}>
                                    <Input
                                       type="text"
                                       placeholder="Ex:- Title"
                                       onChange={this.handleInputChange}
                                       value={title ? title : ""}
                                       name="title"
                                       invalid={errors.title ? true : false}
                                    />
                                    <FormFeedback>
                                       {errors.title ? errors.title : null}
                                    </FormFeedback>
                                 </div>
                              </InputGroup>
                           </FormGroup>
                        </Col>
                        <Col md="6">
                           <FormGroup>
                              <InputGroup>
                                 <Label
                                    htmlFor="name"
                                    className="customer-modal-text-style"
                                 >
                                    Description
                                 </Label>
                                 <div className={"input-block"}>
                                    <Input
                                       type="textarea"
                                       placeholder="Ex:- Description"
                                       onChange={this.handleInputChange}
                                       value={description ? description : ""}
                                       name="description"
                                       className="noresize"
                                       invalid={errors.description ? true : false}
                                    />
                                    <FormFeedback>
                                       {errors.description ? errors.description : null}
                                    </FormFeedback>
                                 </div>
                              </InputGroup>
                           </FormGroup>
                        </Col>
                     </Row>
                  </ModalBody>
                  <ModalFooter>
                     <div className="required-fields">*Fields are Required.</div>
                     <Button color="primary" onClick={this.editSet}>
                        Update Set
                     </Button>
                     {" "}
                     <Button color="secondary" onClick={handleSetModal}>
                        Cancel
                     </Button>
                  </ModalFooter>
               </Modal>
            </Form>
         </>
      );
   }
}
