import React, { Component } from "react";
import {
   Table,
   Badge,
   UncontrolledTooltip,
   Form,
   FormGroup,
   Row,
   Col,
   Label,
   InputGroup,
   Input,
   Button
} from "reactstrap";
import Loader from "../../containers/Loader/Loader";
import { formateDate } from "../../Helpers/Date";
import PaginationHelper from "../../Helpers/Pagination";
import { withRouter } from "react-router-dom";
import * as qs from "query-string";
import { AppConfig } from "../../Config/AppConfig";
import { ConfirmBox } from "../../Helpers/SweetAlert";
import { toast } from "react-toastify";
import NoDataFound from "../common/NoFound"

class SetList extends Component {
   constructor(props) {
      super(props);
      this.state = {
         page: 1,
         search: "",
         status: "",
         sort: "",
         openEditModal: false,
         selectedSets: [],
         filterApplied: false,
         bulkAction: "",
         bulkAction1: false,
      };
   }
   componentDidMount() {
      const { location } = this.props;
      const lSearch = location.search;
      const { page, search, sort, status } = qs.parse(lSearch);
      let filterApplied = false;
      if (search || sort) {
         filterApplied = true;
      }
      this.setState({
         page: parseInt(page) || 1,
         sort: sort || "",
         status: status || "",
         search: search || "",
         filterApplied
      });
   }
   componentDidUpdate({ openEdit }) {
      if (this.props.openEdit !== openEdit) {
         this.setState({
            openEditModal: false
         });
      }
   }
   handleChange = e => {
      this.setState({
         [e.target.name]: e.target.value, bulkAction1: false
      });
   };
   onSearch = async (e) => {
      e.preventDefault();
      this.setState({
         page: 1,
         selectedSets: []
      });
      const { search, sort, status, } = this.state;
      let param = {};
      param.page = 1;
      let hasFilter = false;
      if (search) {
         param.search = search;
         hasFilter = true;
      }
      if (sort) {
         param.sort = sort;
      }
      if (status) {
         param.status = status;
         hasFilter = true;
      }
      this.setState({ filterApplied: hasFilter });
      this.props.onSearch(param);
   };
   onReset = e => {
      e.preventDefault();
      this.setState({
         page: 1,
         search: "",
         status: "",
         sort: "",
         selectedSets: [],
         filterApplied: false,
         bulkAction1: false
      });
      this.props.onSearch({});
   };
   onDelete = async (isMultiple = false) => {
      const { value } = await ConfirmBox({
         text: isMultiple
            ? "Do you want to delete selected set(s)?"
            : "Do you want to delete this set?"
      });
      if (!value) {
         this.setState({
            selectedSets: []
         });
         return;
      }
      this.props.onDelete(this.state.selectedSets);
      this.setState({
         selectedSets: [],
         bulkAction: "",
         bulkAction1: false
      })
   };
   editSet = set => {
      this.props.modelOperate({
         set,
         editSetModal: true
      });

   };
   handleCheckboxChnage = e => {
      const { target } = e;
      const { checked, value } = target;
      const { selectedSets } = this.state;
      if (checked) {
         selectedSets.push(value);
         this.setState({
            selectedSets
         });
         return;
      }
      const index = selectedSets.indexOf(value);
      selectedSets.splice(index, 1);
      this.setState({
         selectedSets
      });
   };
   handleCheckAllCheckBox = e => {
      const { bulkAction1, selectedSets } = this.state
      const { target } = e;
      if (bulkAction1) {
         this.setState({ bulkAction1: false })
      } else {
         this.setState({ bulkAction1: true })
      }
      const { checked } = target;
      if (!checked) {
         this.setState({
            selectedSets: [],
            bulkAction: ""
         });
         return;
      }
      const { setData } = this.props;
      const { sets } = setData;
      sets.forEach(set => {
         selectedSets.push(set._id);
      });
   };
   activateSets = async (isMultiple = false) => {
      const { value } = await ConfirmBox({
         text: isMultiple
            ? "Do you want to active selected set(s)?"
            : "Do you want to active this set?"
      });
      if (!value) {
         this.setState({
            selectedSets: [],
            bulkAction: "",
            bulkAction1: false
         });
         return;
      }
      this.props.onStatusUpdate({ status: 1, sets: this.state.selectedSets });
      this.setState({
         selectedSets: [],
         bulkAction: "",
         bulkAction1: false
      })
   };
   deactivateSets = async (isMultiple = false) => {
      const { value } = await ConfirmBox({
         text: isMultiple
            ? "Do you want to inactive selected set(s)?"
            : "Do you want to inactive this set?"
      });
      if (!value) {
         this.setState({
            selectedSets: [],
            bulkAction: "",
            bulkAction1: false
         });
         return;
      }
      this.props.onStatusUpdate({ status: 0, sets: this.state.selectedSets });
      this.setState({
         selectedSets: [],
         bulkAction: "",
         bulkAction1: false
      })
   };
   handleActionChange = e => {
      const { selectedSets } = this.state;
      const { target } = e;
      const { value } = target;
      this.setState({
         bulkAction: value
      });
      if (!value) {
         return;
      }
      if (!selectedSets.length) {
         toast.error("Please select at least one sets.");
         this.setState({
            bulkAction: "",
            bulkAction1: false
         })
         return;
      }
      if (value === "active") {
         this.activateSets(true);
      } else if (value === "inactive") {
         this.deactivateSets(true);
      } else if (value === "delete") {
         this.onDelete(true);
      }
   };
   render() {
      const { setData, } = this.props;
      const { sets, isLoading, totalSets } = setData;
      const {
         page,
         search,
         sort,
         status,
         selectedSets,
         filterApplied,
         bulkAction,
         bulkAction1
      } = this.state;
      return (
         <>
            <div className={"filter-block"}>
               <Form onSubmit={this.onSearch}>
                  <Row>
                     <Col lg={"3"} md={"3"} className="mb-0">
                        <FormGroup className="mb-0">
                           <InputGroup className="mb-2">
                              <input
                                 type="text"
                                 name="search"
                                 onChange={this.handleChange}
                                 className="form-control"
                                 value={search}
                                 aria-describedby="searchSet"
                                 placeholder="Search by set title"
                              />
                           </InputGroup>
                        </FormGroup>
                     </Col>
                     <Col lg={"2"} md={"2"} className="mb-0">
                        <FormGroup className="mb-0">
                           <Input
                              type="select"
                              name="status"
                              id="exampleSelect"
                              onChange={this.handleChange}
                              value={status}
                           >
                              <option className="form-control" value={""}>
                                 Sets Status
                              </option>
                              <option value={1}>Active</option>
                              <option value={0}>Inactive</option>
                           </Input>
                        </FormGroup>
                     </Col>
                     <Col lg={"2"} md={"2"} className="mb-0">
                        <FormGroup className="mb-0">
                           <Input
                              type="select"
                              name="sort"
                              id="SortFilter"
                              onChange={this.handleChange}
                              value={sort}
                           >
                              <option className="form-control" value={""}>
                                 Sort By
                              </option>
                              <option value={"createddesc"}>Sort by New</option>
                              <option value={"createdasc"}>Sort by Old</option>
                              <option value={"nasc"}>Sort by A-Z</option>
                              <option value={"ndesc"}>Sort by Z-A</option>
                           </Input>
                        </FormGroup>
                     </Col>
                     <Col lg={"5"} md={"5"} className="mb-0">
                        <Row>
                           <Col lg={"6"} md={"6"} className="mb-0">
                              <div className="filter-btn-wrap">
                                 <Label className="height17 label" />
                                 <div className="form-group mb-0">
                                    <span className="mr-2">
                                       <Button
                                          type="submit"
                                          className="btn btn-theme-transparent"
                                          id="Tooltip-1"
                                       >
                                          <i className="icons cui-magnifying-glass"></i>
                                       </Button>
                                       <UncontrolledTooltip target="Tooltip-1">
                                          Search
                                       </UncontrolledTooltip>
                                    </span>
                                    <span className="">
                                       <Button
                                          type="button"
                                          className="btn btn-theme-transparent"
                                          id="Tooltip-2"
                                          onClick={this.onReset}
                                       >
                                          <i className="icon-refresh icons"></i>
                                       </Button>
                                       <UncontrolledTooltip target={"Tooltip-2"}>
                                          Reset all filters
                                       </UncontrolledTooltip>
                                    </span>
                                 </div>
                              </div>
                           </Col>
                        </Row>
                     </Col>
                  </Row>
               </Form>
            </div>
            <Table responsive hover>
               <thead>
                  <tr>
                     <th style={{ maxWidth: "130px" }}>
                        <div className="table-checkbox-wrap">
                           <span className="checkboxli checkbox-custom checkbox-default">
                              <Input
                                 type="checkbox"
                                 name="checkbox"
                                 id="checkAll"
                                 disabled={!sets.length}
                                 onChange={this.handleCheckAllCheckBox}
                                 checked={
                                    (selectedSets.length === sets.length && sets.length) || bulkAction1
                                 }
                              />
                              <label className="" htmlFor="checkAll" />
                           </span>
                           <Input
                              className="commonstatus"
                              type="select"
                              id="exampleSelect"
                              onChange={this.handleActionChange}
                              disabled={!sets.length}
                              value={bulkAction}
                           >
                              <option value={""}>Select</option>
                              {sets.length ? (
                                 <>
                                    <option value={"active"}>Active</option>
                                    <option value={"inactive"}>Inactive</option>
                                 </>
                              ) : null}
                           </Input>
                        </div>
                     </th>
                     <th style={{ maxWidth: "180px" }}><i className={"fas fa-users"} /> Title</th>
                     <th style={{ maxWidth: "150px" }}><i className={"far fa-building"} /> Description</th>
                     <th style={{ maxWidth: "130px" }}><i className={"far fa-folder"} /> Folder</th>
                     <th style={{ maxWidth: "90px" }} className={"text-center"}><i className={"fa fa-exclamation-circle"} /> Status</th>
                     <th style={{ maxWidth: "130px" }}><i className={"fas fa-clock"} /> Created /  Updated Details</th>
                     <th style={{ maxWidth: "180px" }} className={"text-center"}>Action</th>
                  </tr>
               </thead>
               <tbody>
                  {!isLoading ? (
                     sets.length ? (
                        sets.map((set, index) => {
                           return (
                              <tr key={index}>
                                 <td style={{ maxWidth: "130px" }}>
                                    <div className="checkbox-custom checkbox-default coloum-checkbox">
                                       <Input
                                          type="checkbox"
                                          value={set._id}
                                          checked={selectedSets.indexOf(set._id) > -1 || bulkAction1}
                                          name="checkbox"
                                          onChange={this.handleCheckboxChnage}
                                       />
                                       <label htmlFor={set._id}>
                                          {(page - 1) * AppConfig.ITEMS_PER_PAGE + index + 1}.
                                </label>
                                    </div>
                                 </td>
                                 <td style={{ maxWidth: "180px" }}>
                                    {set.title ? set.title : <span>N/A</span>}
                                 </td>
                                 <td style={{ maxWidth: "150px" }} className={"text-capitalize"}>
                                    {set.description ? set.description : <span>N/A</span>}
                                 </td>
                                 <td style={{ maxWidth: "130px" }} >
                                    {set.folderId && set.folderId !== null ? set.folderId.title : <span>N/A</span>}
                                 </td>
                                 <td style={{ maxWidth: "90px" }} className={"text-center"}>
                                    {set.status ? (
                                       <>
                                          <Badge
                                             className={"badge-button"}
                                             color="success"
                                             id={`setStatus-${set._id}`}
                                             onClick={() => {
                                                this.setState(
                                                   {
                                                      selectedSets: [set._id]
                                                   },
                                                   () => {
                                                      this.deactivateSets();
                                                   }
                                                );
                                             }}
                                          >
                                             Active
                                          </Badge>
                                          <UncontrolledTooltip target={`setStatus-${set._id}`}>
                                             Inactivate {set.title}
                                          </UncontrolledTooltip>
                                       </>
                                    ) : (
                                          <>
                                             <Badge
                                                className={"badge-button"}
                                                color="danger"
                                                id={`setStatus1-${set._id}`}
                                                onClick={() => {
                                                   this.setState(
                                                      {
                                                         selectedSets: [set._id]
                                                      },
                                                      () => {
                                                         this.activateSets();
                                                      }
                                                   );
                                                }}
                                             >
                                                Inactive
                                             </Badge>
                                             <UncontrolledTooltip target={`setStatus1-${set._id}`}>
                                                Activate {set.title}
                                             </UncontrolledTooltip>
                                          </>
                                       )}
                                 </td>
                                 <td style={{ maxWidth: "130px" }}>
                                    <div id={`create${index}`}>
                                       {set.createdAt ? formateDate(set.createdAt) : <span>N/A</span>}
                                       {set.createdAt ?
                                          <UncontrolledTooltip target={`create${index}`}>
                                             Created At
                                          </UncontrolledTooltip> : null}
                                    </div>
                                    <div id={`update-${index}`}>
                                       {set.updatedAt ? formateDate(set.updatedAt) : <span>N/A</span>}
                                       {set.updatedAt ?
                                          <UncontrolledTooltip target={`update-${index}`}>
                                             Update At
                                          </UncontrolledTooltip> : null}
                                    </div>
                                 </td>
                                 <td style={{ maxWidth: "180px" }} className={"text-center"}>
                                    <Button
                                       size={"sm"}
                                       onClick={() => this.editSet(set)}
                                       id={`edit-${set._id}`}
                                       className={"btn-theme-transparent"}
                                    >
                                       <i className={"icons cui-pencil"} />
                                    </Button>{" "}
                                    <UncontrolledTooltip target={`edit-${set._id}`}>
                                       Edit
                                    </UncontrolledTooltip>
                                    {" "}
                                    &nbsp;
                                    <Button
                                       size={"sm"}
                                       onClick={() =>
                                          this.setState(
                                             {
                                                selectedSets: [set._id]
                                             },
                                             () => {
                                                this.onDelete();
                                             }
                                          )
                                       }
                                       id={`delete-${set._id}`}
                                       className={"btn-theme-transparent"}
                                    >
                                       <i className={"fa fa-trash"} />
                                    </Button>
                                    <UncontrolledTooltip target={`delete-${set._id}`}>
                                       Delete
                                    </UncontrolledTooltip>
                                 </td>
                              </tr>
                           );
                        })
                     ) : (
                           <tr>
                              <td className={"text-center"} colSpan={12}>
                                 {filterApplied ? <NoDataFound message={"No Sets details found related to your search"} noResult /> :
                                    <NoDataFound message={"Currently there are no Sets details added."} />
                                 }
                              </td>
                           </tr>
                        )
                  ) : (
                        <tr>
                           <td className={"text-center loading"} colSpan={12}>
                              <Loader />
                           </td>
                        </tr>
                     )
                  }
               </tbody>
            </Table>
            {totalSets && !isLoading ? (
               <PaginationHelper
                  totalRecords={totalSets}
                  currentPage={page}
                  onPageChanged={page => {
                     this.setState({ page });
                     this.props.onPageChange(page);
                  }}
                  pageLimit={AppConfig.ITEMS_PER_PAGE}
               />
            ) : null}
         </>
      );
   }
}

export default withRouter(SetList);
